'use client';

import { useEffect, useRef, useCallback } from 'react';
import { createBrowserSupabaseClient } from '@/lib/supabase/client';
import { useAuth } from '@/lib/supabase/auth-provider';

// Í≤ÄÏ¶ù Í∞ÑÍ≤© ÏÑ§Ï†ï (Í∏∞Î≥∏: 5Î∂Ñ)
const DEFAULT_VERIFICATION_INTERVAL = 5 * 60 * 1000;

// WeChat Í¥ÄÎ†® Ïù∏Ï¶ù ÌÇ§Îì§
const WECHAT_AUTH_KEYS = [
  'wechat_auth_token',
  'wechat_auth_state', 
  'wechat_login_state',
];

export interface PeriodicAuthVerificationOptions {
  /**
   * Í≤ÄÏ¶ù Í∞ÑÍ≤© (Î∞ÄÎ¶¨Ï¥à)
   * @default 300000 (5Î∂Ñ)
   */
  interval?: number;
  
  /**
   * Ïù∏Ï¶ù Ïã§Ìå® Ïãú Ìò∏Ï∂úÎêòÎäî ÏΩúÎ∞±
   */
  onAuthFailure?: (reason: string) => void;
  
  /**
   * Í≤ÄÏ¶ù ÏÑ±Í≥µ Ïãú Ìò∏Ï∂úÎêòÎäî ÏΩúÎ∞±
   */
  onAuthSuccess?: () => void;
  
  /**
   * ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•ò Ïãú Ìò∏Ï∂úÎêòÎäî ÏΩúÎ∞±
   */
  onNetworkError?: (error: Error) => void;
  
  /**
   * ÌõÖ ÌôúÏÑ±Ìôî Ïó¨Î∂Ä
   * @default true
   */
  enabled?: boolean;
  
  /**
   * WeChat Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨ Ïó¨Î∂Ä
   * @default true
   */
  includeWeChatVerification?: boolean;
}

/**
 * Ï£ºÍ∏∞Ï†ÅÏúºÎ°ú Ïù∏Ï¶ù ÏÉÅÌÉúÎ•º Í≤ÄÏ¶ùÌïòÎäî Ïª§Ïä§ÌÖÄ ÌõÖ
 * 
 * @param options Í≤ÄÏ¶ù ÏòµÏÖò
 * @returns Í≤ÄÏ¶ù Í¥ÄÎ†® Ìï®ÏàòÎì§Í≥º ÏÉÅÌÉú
 */
export function usePeriodicAuthVerification(options: PeriodicAuthVerificationOptions = {}) {
  const {
    interval = DEFAULT_VERIFICATION_INTERVAL,
    onAuthFailure,
    onAuthSuccess,
    onNetworkError,
    enabled = true,
    includeWeChatVerification = true,
  } = options;

  const { isAuthenticated, user, session } = useAuth();
  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const isVerifyingRef = useRef(false);
  const verificationCountRef = useRef(0);
  const lastVerificationRef = useRef<Date | null>(null);

  /**
   * Ï¢ÖÌï©Ï†ÅÏù∏ Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù
   */
  const verifyAuthState = useCallback(async (): Promise<{
    isValid: boolean;
    reason?: string;
    error?: Error;
  }> => {
    try {
      // Ï§ëÎ≥µ Í≤ÄÏ¶ù Î∞©ÏßÄ
      if (isVerifyingRef.current) {
        console.log('üîÑ [PeriodicAuth] Ïù¥ÎØ∏ Í≤ÄÏ¶ù Ï§ë - Ïä§ÌÇµ');
        return { isValid: true };
      }

      isVerifyingRef.current = true;
      verificationCountRef.current += 1;
      lastVerificationRef.current = new Date();

      console.log(`üîç [PeriodicAuth] Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù ÏãúÏûë (${verificationCountRef.current}ÌöåÏ∞®)`, {
        isAuthenticated,
        hasUser: !!user,
        hasSession: !!session,
        timestamp: lastVerificationRef.current.toISOString(),
      });

      // 1. Í∏∞Î≥∏ Ïù∏Ï¶ù ÏÉÅÌÉú Ï≤¥ÌÅ¨
      if (!isAuthenticated || !user || !session) {
        console.warn('‚ùå [PeriodicAuth] Í∏∞Î≥∏ Ïù∏Ï¶ù ÏÉÅÌÉú Ïã§Ìå®');
        return {
          isValid: false,
          reason: 'Í∏∞Î≥∏ Ïù∏Ï¶ù ÏÉÅÌÉúÍ∞Ä Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.',
        };
      }

      // 2. ÏÑ∏ÏÖò ÎßåÎ£å Ï≤¥ÌÅ¨
      if (session.expires_at) {
        const expiryTime = new Date(session.expires_at * 1000);
        const now = new Date();
        const timeUntilExpiry = expiryTime.getTime() - now.getTime();

        if (timeUntilExpiry <= 0) {
          console.warn('‚è∞ [PeriodicAuth] ÏÑ∏ÏÖòÏù¥ ÎßåÎ£åÎê®');
          return {
            isValid: false,
            reason: 'ÏÑ∏ÏÖòÏù¥ ÎßåÎ£åÎêòÏóàÏäµÎãàÎã§.',
          };
        }

        // ÎßåÎ£å 10Î∂Ñ Ï†Ñ Í≤ΩÍ≥†
        if (timeUntilExpiry < 10 * 60 * 1000) {
          console.warn('‚ö†Ô∏è [PeriodicAuth] ÏÑ∏ÏÖòÏù¥ Í≥ß ÎßåÎ£åÎê©ÎãàÎã§:', {
            minutesLeft: Math.floor(timeUntilExpiry / 1000 / 60),
          });
        }
      }

      // 3. WeChat Ïù∏Ï¶ù ÏÉÅÌÉú Ï≤¥ÌÅ¨ (ÏÑ†ÌÉùÏ†Å)
      if (includeWeChatVerification) {
        const provider = session.user?.app_metadata?.provider;
        if (provider === 'wechat') {
          console.log('üîÑ [PeriodicAuth] WeChat Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù');
          
          const wechatTokenValid = WECHAT_AUTH_KEYS.some(key => {
            const value = localStorage.getItem(key);
            return value && value !== 'null' && value !== 'undefined';
          });

          if (!wechatTokenValid) {
            console.warn('üîí [PeriodicAuth] WeChat ÌÜ†ÌÅ∞Ïù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏùå');
            return {
              isValid: false,
              reason: 'WeChat Ïù∏Ï¶ù ÌÜ†ÌÅ∞Ïù¥ Ïú†Ìö®ÌïòÏßÄ ÏïäÏäµÎãàÎã§.',
            };
          }
        }
      }

      // 4. ÏÑúÎ≤ÑÏÇ¨Ïù¥Îìú Ïù∏Ï¶ù Í≤ÄÏ¶ù
      try {
        const response = await fetch('/api/auth/verify', {
          method: 'GET',
          credentials: 'include',
          headers: {
            'Cache-Control': 'no-cache',
          },
        });

        if (!response.ok) {
          console.warn('üö´ [PeriodicAuth] ÏÑúÎ≤Ñ Ïù∏Ï¶ù Í≤ÄÏ¶ù Ïã§Ìå®:', response.status);
          return {
            isValid: false,
            reason: `ÏÑúÎ≤Ñ Ïù∏Ï¶ù Í≤ÄÏ¶ù Ïã§Ìå® (${response.status})`,
          };
        }

        const data = await response.json();
        if (!data.valid) {
          console.warn('‚ùå [PeriodicAuth] ÏÑúÎ≤ÑÏóêÏÑú Ïù∏Ï¶ù Î¨¥Ìö® ÏùëÎãµ:', data.message);
          return {
            isValid: false,
            reason: data.message || 'ÏÑúÎ≤ÑÏóêÏÑú Ïù∏Ï¶ùÏù¥ Î¨¥Ìö®ÌïòÎã§Í≥† ÏùëÎãµÌñàÏäµÎãàÎã§.',
          };
        }

        console.log('‚úÖ [PeriodicAuth] ÏÑúÎ≤Ñ Ïù∏Ï¶ù Í≤ÄÏ¶ù ÏÑ±Í≥µ');
      } catch (error) {
        const networkError = error instanceof Error ? error : new Error('Ïïå Ïàò ÏóÜÎäî ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•ò');
        console.warn('‚ö†Ô∏è [PeriodicAuth] ÏÑúÎ≤Ñ Ïù∏Ï¶ù Í≤ÄÏ¶ù ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•ò:', networkError.message);
        
        // ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•òÎäî Ïù∏Ï¶ù Ïã§Ìå®Î°ú Ï≤òÎ¶¨ÌïòÏßÄ ÏïäÎêò, ÏΩúÎ∞± Ìò∏Ï∂ú
        if (onNetworkError) {
          onNetworkError(networkError);
        }
        
        // ÎÑ§Ìä∏ÏõåÌÅ¨ Ïò§Î•ò ÏãúÏóêÎäî Ïú†Ìö®Ìïú Í≤ÉÏúºÎ°ú Í∞ÑÏ£º (Ïò§ÌîÑÎùºÏù∏ ÌóàÏö©)
        return { isValid: true };
      }

      console.log('‚úÖ [PeriodicAuth] Î™®Îì† Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù ÌÜµÍ≥º');
      return { isValid: true };

    } catch (error) {
      const authError = error instanceof Error ? error : new Error('Ïù∏Ï¶ù Í≤ÄÏ¶ù Ï§ë Ïïå Ïàò ÏóÜÎäî Ïò§Î•ò');
      console.error('üí• [PeriodicAuth] Ïù∏Ï¶ù Í≤ÄÏ¶ù Ï§ë Ïò§Î•ò:', authError);
      
      return {
        isValid: false,
        reason: 'Ïù∏Ï¶ù Í≤ÄÏ¶ù Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.',
        error: authError,
      };
    } finally {
      isVerifyingRef.current = false;
    }
  }, [isAuthenticated, user, session, includeWeChatVerification, onNetworkError]);

  /**
   * ÏàòÎèô Ïù∏Ï¶ù ÏÉÅÌÉú Í≤ÄÏ¶ù
   */
  const manualVerification = useCallback(async () => {
    console.log('üîç [PeriodicAuth] ÏàòÎèô Ïù∏Ï¶ù Í≤ÄÏ¶ù ÏöîÏ≤≠');
    const result = await verifyAuthState();
    
    if (result.isValid) {
      console.log('‚úÖ [PeriodicAuth] ÏàòÎèô Í≤ÄÏ¶ù ÏÑ±Í≥µ');
      onAuthSuccess?.();
    } else {
      console.warn('‚ùå [PeriodicAuth] ÏàòÎèô Í≤ÄÏ¶ù Ïã§Ìå®:', result.reason);
      onAuthFailure?.(result.reason || 'Ïù∏Ï¶ù Í≤ÄÏ¶ù Ïã§Ìå®');
    }

    return result;
  }, [verifyAuthState, onAuthSuccess, onAuthFailure]);

  /**
   * Ï£ºÍ∏∞Ï†Å Í≤ÄÏ¶ù ÏãúÏûë
   */
  const startPeriodicVerification = useCallback(() => {
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
    }

    console.log(`üïê [PeriodicAuth] Ï£ºÍ∏∞Ï†Å Í≤ÄÏ¶ù ÏãúÏûë (${interval / 1000}Ï¥à Í∞ÑÍ≤©)`);

    intervalRef.current = setInterval(async () => {
      console.log('‚è∞ [PeriodicAuth] Ï£ºÍ∏∞Ï†Å Í≤ÄÏ¶ù Ïã§Ìñâ');
      
      const result = await verifyAuthState();
      
      if (result.isValid) {
        console.log('‚úÖ [PeriodicAuth] Ï£ºÍ∏∞Ï†Å Í≤ÄÏ¶ù ÏÑ±Í≥µ');
        onAuthSuccess?.();
      } else {
        console.warn('‚ùå [PeriodicAuth] Ï£ºÍ∏∞Ï†Å Í≤ÄÏ¶ù Ïã§Ìå® - ÏûêÎèô Î°úÍ∑∏ÏïÑÏõÉ ÌïÑÏöî:', result.reason);
        onAuthFailure?.(result.reason || 'Ï£ºÍ∏∞Ï†Å Ïù∏Ï¶ù Í≤ÄÏ¶ù Ïã§Ìå®');
        
        // Í≤ÄÏ¶ù Ïã§Ìå® Ïãú Ï£ºÍ∏∞Ï†Å Í≤ÄÏ¶ù Ï§ëÎã®
        if (intervalRef.current) {
          clearInterval(intervalRef.current);
          intervalRef.current = null;
        }
      }
    }, interval);
  }, [interval, verifyAuthState, onAuthSuccess, onAuthFailure]);

  /**
   * Ï£ºÍ∏∞Ï†Å Í≤ÄÏ¶ù Ï§ëÎã®
   */
  const stopPeriodicVerification = useCallback(() => {
    if (intervalRef.current) {
      console.log('üõë [PeriodicAuth] Ï£ºÍ∏∞Ï†Å Í≤ÄÏ¶ù Ï§ëÎã®');
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }
  }, []);

  // Ïù∏Ï¶ù ÏÉÅÌÉú Î≥ÄÌôî Í∞êÏßÄ
  useEffect(() => {
    if (!enabled) {
      stopPeriodicVerification();
      return;
    }

    if (isAuthenticated && user && session) {
      console.log('üü¢ [PeriodicAuth] Ïù∏Ï¶ùÎêú ÏÉÅÌÉú - Ï£ºÍ∏∞Ï†Å Í≤ÄÏ¶ù ÏãúÏûë');
      startPeriodicVerification();
    } else {
      console.log('üî¥ [PeriodicAuth] ÎπÑÏù∏Ï¶ù ÏÉÅÌÉú - Ï£ºÍ∏∞Ï†Å Í≤ÄÏ¶ù Ï§ëÎã®');
      stopPeriodicVerification();
    }

    return () => {
      stopPeriodicVerification();
    };
  }, [enabled, isAuthenticated, user, session, startPeriodicVerification, stopPeriodicVerification]);

  // Ïª¥Ìè¨ÎÑåÌä∏ Ïñ∏ÎßàÏö¥Ìä∏ Ïãú Ï†ïÎ¶¨
  useEffect(() => {
    return () => {
      stopPeriodicVerification();
    };
  }, [stopPeriodicVerification]);

  // ÌéòÏù¥ÏßÄ Í∞ÄÏãúÏÑ± Î≥ÄÌôî Í∞êÏßÄ
  useEffect(() => {
    if (!enabled || !isAuthenticated) return;

    const handleVisibilityChange = () => {
      if (document.visibilityState === 'visible') {
        console.log('üëÅÔ∏è [PeriodicAuth] ÌéòÏù¥ÏßÄ Í∞ÄÏãúÏÑ± Î≥µÍµ¨ - Ï¶âÏãú Í≤ÄÏ¶ù Ïã§Ìñâ');
        manualVerification();
      }
    };

    document.addEventListener('visibilitychange', handleVisibilityChange);

    return () => {
      document.removeEventListener('visibilitychange', handleVisibilityChange);
    };
  }, [enabled, isAuthenticated, manualVerification]);

  return {
    // ÏÉÅÌÉú
    isVerifying: isVerifyingRef.current,
    verificationCount: verificationCountRef.current,
    lastVerification: lastVerificationRef.current,
    
    // Î©îÏÑúÎìú
    manualVerification,
    startPeriodicVerification,
    stopPeriodicVerification,
    
    // Ïú†Ìã∏Î¶¨Ìã∞
    verifyAuthState,
  };
}